import "../css/Map.css"
import { useState, useEffect} from "react";


//Leaflet Imports
import "leaflet/dist/leaflet.css"
import { MapContainer, Marker, Popup, TileLayer, Polyline } from "react-leaflet"
import { Icon } from "leaflet"

//use a promise to auto zoom on user location


const Map = () => {

  //states 
  const [currentLocation, setCurrentLocation] = useState([[0, 0]])

  const polyline = [
    [51.505, -0.09],
    [51.51, -0.1],
    [51.51, -0.12],
  ]


  //lifecycle
  useEffect(() => {
    getLocation()
  }, [])

  
  // this is the gelocation method to get the location from the browser
  const getLocation = () => {
    if(navigator.geolocation){
  
      const success = (pos) => {
        const lat = pos.coords.latitude
        const lon = pos.coords.longitude
        setCurrentLocation(prevLocation => ([...prevLocation, [lat, lon]]))

      }
  
      const error = (err) => console.error(err)
     
  
      let options = {
        enableHighAccuracy: false,
        timeout: 1000,
        maximumAge: 0,
        distanceFilter: 1
      }
  

        navigator.geolocation.watchPosition(success, error, options)
      // setInterval(() => {
      //   // const watchPosition = navigator.geolocation.watchPosition(success, error, options)
      //   navigator.geolocation.getCurrentPosition(success, error)
      // }, 1000);
    }
  }
  

  const icon = new Icon({
    iconUrl: "data:image/png;base64,",
    iconSize: [25, 25]
  })

  console.log(currentLocation.length-1, currentLocation[currentLocation.length-1])

  return (
    <div id="map" className="map-container">
      <MapContainer style={{height: "80vh"}} zoom={4} center={[-27, 26]}>

          <TileLayer
          attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />

        <Marker position={currentLocation[currentLocation.length-1]} icon={icon}></Marker>

        <Polyline positions={currentLocation}/>

      </MapContainer>
      {currentLocation.map(location => {
        return <div>
            location: {location[0]} longitude: {location[1]}
        </div>
      })}
     </div>
  )
}


export default Map
